/* ---------------------------------------------------
      SIDEBAR STYLE
  ----------------------------------------------------- */
.sidebar {
  min-width: 250px;
  max-width: 250px;
  background: rgba(18, 76, 193, 1);
  color: #fff;
  margin-left: -250px;
  transition: all 0.5s;
  
}

.sidebar.is-open {
  margin-left: 0;
  transition: 0.5s;
}

.sidebar-header {
  background: rgba(2, 0, 33, 1);
}

.sidebar-header h3 {
  color: #fff;
  padding: 1em;
}

.sidebar ul p {
  color: #fff;
  padding: 10px;
}

.menu-open {
  background: rgba(73, 130, 246, 1);
}

/* .nav-item:hover {
    color: darkgreen;
    background: #fff;
}

*/

.items-menu {
  color: #fff;
  background: rgba(73, 130, 246, 1);
}

/* ADD IF REQUIRED */
/* li a.dropdown-toggle::after {
    display: inline-flex;
