    left: 30%;
    top: 10%;
}

*/

.sidebar-header > span {
  position: relative;
  float: right;
  margin: 0.5em;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}

.side-menu {
  height: calc(100vh - 130px);
  overflow-y: scroll;
  background: rgba(2, 0, 33, 1);
}

.side-menu::-webkit-scrollbar {
  width: 10px;
}

.side-menu::-webkit-scrollbar-thumb {
  background: rgba(73, 130, 246, 1);
  border-radius: 3px;
}

.side-menu::-webkit-scrollbar-thumb:hover {
  background:rgba(73, 130, 246, 1);
}

/* ---------------------------------------------------
      CONTENT STYLE
  ----------------------------------------------------- */
.content_db {
  padding: 20px;
  margin-left: 0;
  height: 100vh;
}

@media only screen and (max-width: 820px) {
  /* .Db {
    overflow: hidden;
  } */



  .sidebar.is-open {
    min-width: 35%;
    /* max-width: 100%; */
    margin-left: 0;
    transition: all 0.5s, height 0s;
    height: 100vh;
    position: absolute;
  }

  .sidebar.is-open > .sidebar-header span {
    display: unset;
  }

  li a.dropdown-toggle::after {
    display: inline-block;
    position: relative;
    left: 30%;
  }
}
@media (max-width:670px) {
  .content_db {
    height: 100vh;
  }
 

}
@media (max-width:998px) {
  .content_db {
    height: 100%;
  }
}
